.habitCard {
    margin: 20px;
    padding: 20px;
    border-radius: 10px;
    font-size: 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.habitCard:nth-child(3n + 1) {
    background-image: linear-gradient(transparent, #ffc9c4);
}
.habitCard:nth-child(3n + 2) {
    background-image: linear-gradient(transparent, #c4ffc4);
}
.habitCard:nth-child(3n + 3) {
    background-image: linear-gradient(transparent, #cfc4ff);
}

.fa-face-laugh {
    color: tomato;
    margin-right: 10px;
}

.subtitle {
    font-size: 20px;
    color: gray;
}

.fa-circle-plus,
.fa-circle-minus,
.fa-trash-can {
    /* Chrome, Safari, Opera */
    -webkit-animation: rainbow 10s infinite;

    /* Internet Explorer */
    -ms-animation: rainbow 10s infinite;

    /* Standar Syntax */
    animation: rainbow 10s infinite;
    margin-right: 10px;
}

.habits-reset {
    margin: 20px;
    padding: 20px;
    border: 0;
    border-radius: 10px;
    background-color: bisque;
}

/* Chrome, Safari, Opera */
@-webkit-keyframes rainbow {
    0% {
        color: rgb(255, 235, 180);
    }
    10% {
        color: rgb(255, 191, 180);
    }
    20% {
        color: rgb(180, 191, 255);
    }
    30% {
        color: rgb(180, 255, 180);
    }
    40% {
        color: rgb(255, 180, 211);
    }
}

/* Internet Explorer */
@-ms-keyframes rainbow {
    0% {
        color: rgb(255, 235, 180);
    }
    10% {
        color: rgb(255, 191, 180);
    }
    20% {
        color: rgb(180, 191, 255);
    }
    30% {
        color: rgb(180, 255, 180);
    }
    40% {
        color: rgb(255, 180, 211);
    }
}

/* Standar Syntax */
@keyframes rainbow {
    0% {
        color: rgb(255, 235, 180);
    }
    10% {
        color: rgb(255, 191, 180);
    }
    20% {
        color: rgb(180, 191, 255);
    }
    30% {
        color: rgb(180, 255, 180);
    }
    40% {
        color: rgb(255, 180, 211);
    }
}
